
.card-container {
    display: flex;
    margin-top: 2%;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  
.card{
    margin: auto;
    width: 60vw;
    padding: 50px;
    margin-top: 4%;
    background-color: rgb(255,255,255);
    border-radius: 10px;
    box-shadow: rgba(17,17, 26, 0.05) 0px 1px 0px, rgba(
        17, 17, 26, 0.1) 0px 8px 8px;
}
.card-main-heading{
    text-align: center;
}


.card-question{
    cursor: pointer;
    margin: 15px;
    width: 53vw;
    padding: 10px;
    margin-left: -28px;
    background-color: rgb(255,255,255);
    border-radius: 10px;
    box-shadow: rgba(17,17, 26, 0.05) 0px 1px 0px, rgba(
        17, 17, 26, 0.1) 0px 8px 8px;
}

.card-question .question{
    display: inline-flex;
    margin-right: 10px;
    font-weight: 600;
    font-size: 1.2rem;
    text-align: justify;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.card-answer{
    padding: 30px 60px 40px 70px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 1rem;
    font-weight: 400;   
    text-align: justify;
    margin-left: -50px;
    color: rgb(40,37,53);
}


@media screen and (max-width: 820px){
    .card{
        width: 70vw;
        padding: 50px;
    }
    .card-main-heading{
        text-align: center;
        font-size: 16px;
    }
    .card-question{
        width: 62vw;
        margin-left: -50px;
    }
    .card-question .question{
        font-weight: 600;
        font-size: 12px;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }
    .card-answer{
        padding: 10px 0px 16px 0px;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        font-size: 12px;
        font-weight: 400;   
        text-align: justify;
        margin-left: -20px;
        margin-right: 0px;
        color: rgb(40,37,53);
    }
}
