

.pnf {
    display: flex;
    min-height: 80vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .pnf-title {
    font-size: 100px;
    font-weight: 700;
  }
  
  .pnf-heading {
    font-weight: normal;
  }
  
  .pnf-btn {
    color: black;
    border: 1px solid black;
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;
  }
  .pnf-btn:hover {
    background-color: black;
    color: white;
  }


  @media screen and (max-width: 820px){
    .pnf {
        min-height: 83.9vh;
    }
    .pnf-title {
        font-size: 70px;
    }
    .pnf-heading {
        font-size: 20px;
      }
}