*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Source Sans Pro', sans-serif;
}
.item-inside{
    box-shadow:  1px 4px 3px 0px rgb(166, 166, 166);
    border-radius: 10px;
}
.card1{
    height: 25% !important;
}
.widthadjus{
   box-shadow: 0 10px 10px -10px #333 !important;
   border-bottom: 1px solid #1f1209;
