   /* margin: -50px auto 10px; */
}
hr{
    border : none;
    height: 20px;
    width: 90%;
    height: 50px;
    margin-top: 10px;
    border-bottom: 1px solid #1f1209;
    box-shadow: 0 20px 20px -30px #333;
    margin: -50px auto 10px;
}
.line-clamp{
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.dashboard-details-name{
    font-size: 1.1rem;
}