
*{
    margin: 0;
    padding: 0;
}
.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    margin-top: 7%;
}
.linkk{
    color: rgb(42, 66, 145);
}
.kbox{
background-color: white;
padding-left: 5%;
padding-top: 10%;
padding-bottom: 10%;
padding-right: 5%;
border: solid rgb(190, 182, 182);
/* box-shadow:-0 0 0.5rem rgba(0,0,0,0.5) ; */
border-radius: 2.5%;
width: 35%;
}
.intro{
    margin-top: -15%;
    padding-bottom: 4%;
    margin-bottom: 5%;
}
.signup{
    text-align: center;
    color: rgb(42, 66, 145);
    font-size: small;
}

.input-div{
    margin-bottom: 8%;
}

.login{
    margin-bottom: 2%;
    /* width: 40% !important; */
}

.loginbutton{
    margin-top: 5%;
}


@media screen and (max-width: 820px){
    .login-container {
        height: 50rem;
    }
    .kbox{ 
        width: 100%;
    }
    .intro{
        margin-top: 10%;
    }
}