.dashboard-heading{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 2.2rem;
    border: none;
}

.dashboard-details{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 1.1rem;
}
.dashboard-details-name{
    font-weight: 500;
}


@media screen and (max-width: 820px){
    .dashboard-main{
      height: 40rem;
    }
    .dashboard-heading{
        font-size: 1rem;
        font-weight: 700;
    }
    .dashboard-details{
        font-size: 0.9rem;
        font-weight: 500;
    }
    .dashboard-details-name{
        font-size: 0.9rem;
        font-weight: 500;
    }
}