.footer {
  background-color: #f2f2f2;
  padding-top: 1%;
  position: absolute;
  /* bottom: 0; */
  width: 100%;
  text-align: center;
  margin-top: auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

/* .footer .container p{
  font-family: 'Poppins', sans-serif;
} */

 .nav-link{
  /* font-family: 'Poppins', sans-serif; */
  display: inline-block;
  margin-inline: 5px;
  text-align: center;
}

.nav-link:hover{
  color: rgb(10, 47, 167);
}

@media screen and (max-width: 768px) {
  .navbar-brand{
    font-size: 15px;
  }
  .navbar-toggler-icon{
    height: 30px;
  }
  .container {
    max-width: 90%;
  }
  .footer .container p{
    font-size: 10px;
  }
   .nav-link{
    font-size: 12.5px;
  }
}


